@import 'vars';

  .client {
    width: 100%;
    margin-top: 40px;
    display: grid;
    place-items: center;
    justify-content: center;
  }

  .care {
    display: flex;
    width: 40%;
    margin-top: 40px;
  }

  .form-container-2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 900px;
  }


  .logo {
    width: 150px;
    margin-bottom: 48px;
    justify-self: center;
    display: none;
  }
  .title {
    font-size: var(--lg);
    margin-bottom: 15px;
    text-align: center;
  }
  .subtitle {
    color: var(--very-light-pink);
    font-size: var(--md);
    font-weight: 300;
    margin-top: 0;
    margin-bottom: 32px;
    text-align: center;
  }

  .form-inputs {
    display: grid;
    grid-template-columns: auto 1fr auto;
  }

  .label {
    font-size: var(--sm);
    font-weight: bold;
    margin-bottom: 4px;
    color: black;
  }
  .input {
    background-color: var(--text-input-field);
    border-radius: 8px;
    height: 30px;
    font-size: var(--md);
    padding: 6px;
    margin-bottom: 12px;
  }

 
  input[type=text]:focus {
    border: 1px solid #af81db;
  }

  .primary-button {
    background-color: #af81db;
    border-radius: 8px;
    border: none;
    color: var(--white);
    width: 100%;
    cursor: pointer;
    font-size: var(--md);
    font-weight: bold;
    height: 50px;
    margin-bottom: 8px;
  }

  .secondary-button {
    background-color: #525353;
    border-radius: 8px;
    border: none;
    color: var(--white);
    width: 100%;
    cursor: pointer;
    font-size: var(--md);
    font-weight: bold;
    height: 50px;
  }

  @media (max-width: 640px) {
    .logo {
      display: block;
    }
  }


  .container-check {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
  }

  .container-check input {
    position: absolute;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
  }

  /* On mouse-over, add a grey background color */
.container-check:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.container-check input:checked ~ .checkmark {
  background-color: #2196F3;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.container-check input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.container-check .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.wrapper {
  display: flex;
  flex-direction: row;
}

.textarea {

height: 15%;
}